body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #07de44, #00276a);
  color: #fff;
}

.App {
  text-align: center;
  background: rgba(255, 255, 255, 0.2);
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  color: #fff;
  margin-top: 10px;
  margin-bottom: 20px;
  font-size: 2.5em;
}

input[type="url"] {
  margin: 20px 0;
  padding: 15px;
  width: 80%;
  max-width: 400px;
  border: none;
  border-radius: 5px;
  font-size: 1em;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  outline: none;
  text-align: center;
}

button {
  padding: 15px 30px;
  font-size: 1em;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  outline: none;
}

button:hover {
  background-color: #0056b3;
}

.logo {
  width: 300px;
  height: auto;
  align-items: center;
}

a {
  color: #fff;
  text-decoration: none;
  font-size: 1.2em;
  display: inline-block;
  margin-top: 20px;
}

a:hover {
  text-decoration: underline;
}
